import React from 'react'
import {
    Form,
    Input,
    Tooltip,
    Icon,
    Select,
    Button,
    Card,
    Upload,
    message,
} from 'antd';
import {reqForm} from './api'

const { Option } = Select;

class biaodan extends React.Component {
    state = {
        confirmDirty: false,
        loading: false,
        data:{},

    };

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, data) => {
            if (!err) {
                if (data.picture!==null){
                    this.setState(data);
                    reqForm(data,this.state.imageUrl)
                    console.log(reqForm.msg)
                }
                else {
                    alert("请选择图片")
                }
            }
        });
    };


    getBase64(img, callback) {
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
    }


    beforeUpload(file) {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
        if (!isJpgOrPng) {
            message.error('你只能上传 JPG/PNG 格式的文件!');
        }
        const isLt5M = file.size / 1024 / 1024 < 5;
        if (!isLt5M) {
            message.error('图片大小不能超过 5MB!');
        }
        return isJpgOrPng && isLt5M;
    }

    handleChange = info => {

            this.getBase64(info.file.originFileObj, imageUrl =>
                this.setState({
                    imageUrl,
                    loading: false,
                }),
            );
       // }
    };


    render() {
        const { getFieldDecorator } = this.props.form;

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };
        const prefixSelector = getFieldDecorator('prefix', {
            initialValue: '86',
        })(
            <Select style={{ width: 70 }}>
                <Option value="86">+86</Option>
            </Select>,
        );


        const uploadButton = (
            <div>
                <Icon type={this.state.loading ? 'loading' : 'plus'} />
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        const { imageUrl } = this.state;

        return (
            <Card title='报名表填写'>
                <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                    <div style={{textAlign:'center'}}><span style={{color:'red'}}>注：</span>截止提交时间是9月13日中午12点</div>
                    <Form.Item label='照片'>
                        <Upload
                            name="file"
                            listType="picture-card"
                            showUploadList={false}
                            action="/recruit/table/add"
                            beforeUpload={this.beforeUpload}
                            onChange={this.handleChange}
                        >
                            {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
                        </Upload>
                    </Form.Item>
                    <Form.Item label='姓名'>
                        {getFieldDecorator('name', {
                            rules: [{ required: true, max:10, message: '请输入姓名!(最多10个字符)', whitespace: true }],
                        })(<Input />)}
                    </Form.Item>
                    <Form.Item label='姓别'>
                        {getFieldDecorator('gender', {
                            rules: [{ required: true, len:1, message: '请输入姓别!(请输入男或女)', whitespace: true }],
                        })(<Input />)}
                    </Form.Item>
                    <Form.Item label="手机">
                        {getFieldDecorator('phonenum', {
                            rules: [{ required: true, len:11, message: '请输入正确的手机号码！' }],
                        })(<Input addonBefore={prefixSelector} style={{ width: '100%' }} />)}
                    </Form.Item>
                    <Form.Item label='QQ'>
                        {getFieldDecorator('qqnum', {
                            rules: [{ required: true, max:11, message: '请输入正确的QQ号码!', whitespace: true }],
                        })(<Input />)}
                    </Form.Item>
                    <Form.Item label='班级' help='如：软件工程1901'>
                        {getFieldDecorator('classes', {
                            rules: [{ required: true, max:10, message: '请输入正确的班级信息!', whitespace: true }],
                        })(<Input />)}
                    </Form.Item>
                    <Form.Item label='学号'>
                        {getFieldDecorator('dormitory', {
                            rules: [{ required: true, len:12, message: '请输入正确的学号!', whitespace: true }],
                        })(<Input />)}
                    </Form.Item>
                    <Form.Item
                        label={
                            <span>
              已投递组织&nbsp;
                                <Tooltip title="已经报名过的学校社团或组织">
                <Icon type="question-circle-o" />
              </Tooltip>
            </span>
                        }
                        help='没有请填写’暂无‘'
                    >
                        {getFieldDecorator('organization', {
                            rules: [{ required: true, max:251, message: '没有请填写’暂无‘(最多251字)', whitespace: true }],
                        })(<Input.TextArea autosize={{ minRows: 3, maxRows: 6 }}  />)}
                    </Form.Item>

                    <Form.Item label='自我介绍'>
                        {getFieldDecorator('introduction', {
                            rules: [{ required: true,max:251, message: '此项为必填项!(最多251字)', whitespace: true }],
                        })(<Input.TextArea autosize={{ minRows: 3, maxRows: 6 }}  />)}
                    </Form.Item>
                    <Form.Item label='IT相关爱好、特长'>
                        {getFieldDecorator('likes', {
                            rules: [{ required: true,max:251, message: '此项为必填项!(最多251字)', whitespace: true }],
                        })(<Input.TextArea autosize={{ minRows: 3, maxRows: 6 }}  />)}
                    </Form.Item>
                    <Form.Item label='在实验室的学习展望'>
                        {getFieldDecorator('future', {
                            rules: [{ required: true,max:251, message: '此项为必填项!(最多251字)', whitespace: true }],
                        })(<Input.TextArea autosize={{ minRows: 3, maxRows: 6 }}  />)}
                    </Form.Item>
                    <Form.Item label='理想目标'>
                        {getFieldDecorator('traget', {
                            rules: [{ required: true,max:251, message: '此项为必填项!(最多251字)', whitespace: true }],
                        })(<Input.TextArea autosize={{ minRows: 3, maxRows: 6 }}  />)}
                    </Form.Item>



                    <Form.Item {...tailFormItemLayout}>
                        <Button type="primary" htmlType="submit" style={{width:300}}>
                            提交
                        </Button>
                    </Form.Item>
                    <div style={{textAlign:'center'}}>更多信息请加招新群：572272821询问 </div>
                    <div style={{textAlign:'center'}}><a href='http://shang.qq.com/wpa/qunwpa?idkey=286f620c2b57885df646fbe11f4985f695cb16218a1eb3d017b48616e0bbef94' ><Icon type="qq" style={{ fontSize: '25px', color: '#08c' }}/>点击加群</a></div>
                </Form>
            </Card>
        )
    }
}
const WrappedRegistrationForm = Form.create({})(biaodan);

export default (WrappedRegistrationForm)