import axios from 'axios'

//提交数据
export const reqForm = (data, file) =>
    axios.post('/recruit/table/add',
        {
            "name": data.name,
            "gender": data.gender,
            "phonenum": data.phonenum,
            "qqnum": data.qqnum,
            "classes": data.classes,
            "dormitory": data.dormitory,
            "organization": data.organization,
            "introduction": data.introduction,
            "likes": data.likes,
            "future": data.future,
            "traget": data.traget,
            "picture": file
        })
        .then(function (response) {
            const result = response.data;
            alert(result.msg)
            window.location.href="/recruit/table/index2"
        })
        .catch(function (error) {
            console.log(error);
        });

